import React, { Component } from 'react'
import './UpBtn.css'
import $ from 'jquery'

class UpBtn extends Component {

  componentDidMount() {
    this.scrollToTop()
  }

  scrollToTop() {
    $('.btnAnchor').click(function(e) {
      $('.navbar-collapse').collapse('hide')
      $('html, body').animate({
        scrollTop: 0
      }, 1000, function() {})
      e.preventDefault()
    })
  }

  render() {
    return (
      <div className='col' id='upbutton'  style={{ position: 'absolute', marginTop: '5px'}}>
        <a href='#' className='btnAnchor'>
          <i className='fa fa-angle-double-up'></i>
        </a>
      </div>
    )
  }
}

export default UpBtn



