import React, { Component } from 'react'
import './AboutMe.css'
import sushi from '../../assets/sushi-xs.png'
import ramen from '../../assets/ramen-xs.png'
import horns from '../../assets/horns-xs.png'

class AboutMe extends Component {
  render() {
    return (
      <div id='aboutme' className='section-area text-center'>
        <h2>ABOUT ME</h2><br/>
        <p className='lead text-center'>Hi, my name is <strong>Tin Vo</strong>. I am a software developer specialized in web development and
          mobile development. I love writing code and solving problems. I obtained my <strong>Bachelor's Degree</strong> in <strong>Computer Science </strong>
           at <strong>The University of Texas at Austin <img src={horns} alt='horns' /></strong>&nbsp;Currently, I am working as a software developer at <strong>IBM</strong>. I often play
          video games, watch anime, and listen to music when I have free time.</p>
        <p><strong>Fun facts: <i>I can eat &nbsp; <img src={sushi} alt='sushi' /> &nbsp; and &nbsp; <img src={ramen} alt='ramen'/> &nbsp; everyday.</i></strong></p>
        <br />
      </div>
    )
  }
}

export default AboutMe



