import React, { Component } from 'react'
import './Projects.css'
import project_1 from '../../assets/project_1.png'
import project_2 from '../../assets/project_2.png'
import project_3 from '../../assets/project_3.png'
import project_4 from '../../assets/project_4.png'
import project_5 from '../../assets/project_5.png'
import css from '../../assets/css-3.png'
import html from '../../assets/html5.png'
import javascript from '../../assets/javascript.png'
import angular from '../../assets/angular.png'
import jquery from '../../assets/jquery.png'
import mongodb from '../../assets/mongodb.png'
import swift from '../../assets/swift.png'
import firebase from '../../assets/firebase.png'
import nodejs from '../../assets/nodejs.png'
import docker from '../../assets/docker.png'


class Projects extends Component {
  render() {
    return (
      <div id='projects' className='section-area text-center' >
        <h2>PROJECTS</h2><br/>
        <div className='media row'>
          <div className='col-md-12 col-lg-4'>
            <img src={project_1} className='align-self-center mr-3 img-fluid proj-img' alt='project_1'/>
          </div>
          <div className='col-md-12 col-lg-8 media-body'>
            <h5 className='mt-0 project-title'>CLUTCH INSURANCE</h5>
            <p>
              <i>
                A website that helps customers buy auto insurance. It quickly compares up to sixteen different
                rates and chooses the right one for customers' budget and insurance needs. Plus get matched with
                a local independent agent.
              </i>
            </p>
            <ul className='content-skillz'>
              <li><img src={html} alt='html'/></li>
              <li><img src={css} alt='css' /></li>
              <li><img src={javascript} alt='javascript'/></li>
              <li><img src={angular} alt='angular'/></li>
              <li><img src={mongodb} alt='mongodb'/></li>
              <li><img src={jquery} alt='jquery'/></li>
            </ul>
          </div>
        </div><br/><br/>

        <div className='media row'>
          <div className='col-md-12 col-lg-4 order-lg-2'>
            <img src={project_2} className='align-self-center mr-3 img-fluid proj-img' alt='project_2'/>
          </div>
          <div className='col-md-12 col-lg-8 order-lg-1 media-body'>
            <h5 className='mt-0 project-title'>VENDECOR</h5>
            <p>
              <i>
                Created an iOS app that allows users to buy and sell furnitures. Major responsibilities included
                designing the app, utilizing JSQMessagesViewController for messaging, setting up the database on
                Firebase, registering users, creating the slide-out menu, and testing.
              </i>
            </p>
            <ul className='content-skillz'>
              <li><img src={swift} alt='swift'/></li>
              <li><img src={firebase} alt='firebase'/></li>
            </ul>
          </div>
        </div><br/><br/>

        <div className='media row'>
          <div className='col-md-12 col-lg-4'>
            <img src={project_3} className='align-self-center mr-3 img-fluid proj-img' alt='project_3'/>
          </div>
          <div className='col-md-12 col-lg-8 media-body'>
            <h5 className='mt-0 project-title'>MUZIK</h5>
            <p>
              <i>
                A fully responsive web application built with Angular that allows users to search and listen to their favorite songs and playlists.
              </i>
            </p>
            <ul className='content-skillz'>
              <li><img src={html} alt='html'/></li>
              <li><img src={css} alt='css' /></li>
              <li><img src={javascript} alt='javascript'/></li>
              <li><img src={angular} alt='angular'/></li>
              <li><img src={jquery} alt='jquery'/></li>
              <li><img src={nodejs} alt='nodejs'/></li>
            </ul>
          </div>
        </div><br/><br/>

        <div className='media row'>
          <div className='col-md-12 col-lg-4 order-lg-2'>
            <img src={project_4} className='align-self-center mr-3 img-fluid proj-img' alt='project_4'/>
          </div>
          <div className='col-md-12 col-lg-8 order-lg-1 media-body'>
            <h5 className='mt-0 project-title'>GGMATE</h5>
            <p>
              <i>
                Developed a website that shows an abundance of information and allows extensive search abilityfor games,
                related-companies, people, and platforms. Collected video game data, massaged data to set up the database,
                and created display tables.
              </i>
            </p>
            <ul className='content-skillz'>
              <li><img src={html} alt='html'/></li>
              <li><img src={css} alt='css' /></li>
              <li><img src={javascript} alt='javascript'/></li>
              <li><img src={angular} alt='angular'/></li>
              <li><img src={jquery} alt='jquery'/></li>
              <li><img src={docker} alt='docker'/></li>
            </ul>
           </div>
        </div><br/><br/>

        <div className='media row'>
          <div className='col-md-12 col-lg-4'>
            <img src={project_5} className='align-self-center mr-3 img-fluid proj-img' alt='project_5'/>
          </div>
          <div className='col-md-12 col-lg-8 media-body'>
            <h5 className='mt-0 project-title'>SLANG DICTIONARY</h5>
            <p>
              <i>
                Made a complete dictionary to look up meanings of slang terms, acronyms, and abbreviations
                used in texting, social media, and blogs.
              </i>
            </p>
            <ul className='content-skillz'>
              <li><img src={html} alt='html'/></li>
              <li><img src={css} alt='css' /></li>
              <li><img src={javascript} alt='javascript'/></li>
              <li><img src={angular} alt='angular'/></li>
            </ul>
          </div>
        </div><br/><br/>
      </div>
    )
  }
}

export default Projects



