import $ from 'jquery'

const initialState = { }

const backgroundColor = 'rgba(47, 52, 60, 1.0)'
const backgroundColorTransparent = 'rgba(47, 52, 60, 0.0)'
const textColorWhite = '#FFFFFF'
const borderBottomXS = '0px solid rgba(0,255,255,0.3)'
const borderBottomLG = '2px solid rgba(0,255,255,0.3)'

if($(document).width() < 990) {
  initialState.backgroundColor = backgroundColor
  initialState.textColor = textColorWhite
  // initialState.borderBottom = borderBottomXS
} else {
  initialState.backgroundColor = backgroundColorTransparent
  initialState.textColor = textColorWhite
  // initialState.borderBottom = borderBottomLG
}

export default function NavbarReducer(state=initialState, action={}) {
  switch (action.type) {
    default:
      return state
  }
}



