import React, { Component } from 'react'
import './Skills.css'

class Skills extends Component {
  render() {
    return (
      <div id='skills' className='section-area text-center' >
        <h2>SKILLS</h2><br/>
        <div className='container'>
          <div className='row'>
            <div className='col-md-3 col-sm-6'>
              <ul className='skills-ul'>
                <li className='skills-li'><strong className='skills-header'>LANGUAGES</strong></li>
                <li className='skills-li'>JavaScript (ES6)</li>
                <li className='skills-li'>TypeScript</li>
                <li className='skills-li'>HTML5 / CSS3</li>
                <li className='skills-li'>C / C++ / C#</li>
                <li className='skills-li'>Java</li>
                <li className='skills-li'>Python</li>
                <li className='skills-li'>Swift</li>
              </ul>
            </div>
            <div className='col-md-3 col-sm-6'>
              <ul className='skills-ul'>
                <li className='skills-li'><strong className='skills-header'>FRAMEWORKS</strong></li>
                <li className='skills-li'>Node.js</li>
                <li className='skills-li'>Express.js</li>
                <li className='skills-li'>React/Redux</li>
                <li className='skills-li'>Angular</li>
                <li className='skills-li'>Bootstrap</li>
              </ul>
            </div>
            <div className='col-md-3 col-sm-6'>
              <ul className='skills-ul'>
                <li className='skills-li'><strong className='skills-header'>TOOLS</strong></li>
                <li className='skills-li'>Git / Github</li>
                <li className='skills-li'>Bash</li>
                <li className='skills-li'>React DevTools</li>
                <li className='skills-li'>Redux DevTools</li>
                <li className='skills-li'>Jenkins</li>
                <li className='skills-li'>Postman</li>
                <li className='skills-li'>Unity</li>
              </ul>
            </div>
            <div className='col-md-3 col-sm-6'>
              <ul className='skills-ul'>
                <li className='skills-li'><strong className='skills-header'>OTHERS</strong></li>
                <li className='skills-li'>Design</li>
                <li className='skills-li'>Test Automation</li>
                <li className='skills-li'>UI / UX Testing</li>
                <li className='skills-li'>Mobile Games Dev</li>
              </ul>
            </div>
          </div>
        </div>
        <br />
        <br />
      </div>
    )
  }
}

export default Skills



