import React, { Component } from 'react'
import './Socials.css'
import ramen from '../../assets/ramen-xs.png'
import heart from '../../assets/heart-xs.png'

class Socials extends Component {
  render() {
    return (
      <div className='container-fluid socials'>
        <a href='https://www.linkedin.com/in/tinnvo' target='_blank' rel='noopener noreferrer'><i className='col'></i><i className='fa fa-linkedin-square faico'/></a>
        <a href='https://github.com/tinnvo' target='_blank' rel='noopener noreferrer'><i className='col'></i><i className='fa fa-github-square faico'/></a>
        <a href='https://www.facebook.com/tinnvo' target='_blank' rel='noopener noreferrer'><i className='col'></i><i className='fa fa-facebook-square faico'/></a>
        <a href='https://twitter.com/tinnvo' target='_blank' rel='noopener noreferrer'><i className='col'></i><i className='fa fa-twitter-square faico'/></a>
        <div style={{paddingTop: '15px'}}>
          <span><i>made with <img src={heart} className='footericon' alt='heart'/> and <img src={ramen} className='footericon' alt='ramen'/> by</i></span><br/>
          <span className='name'>TIN NHAT VO </span>
          <span className='copy'>&copy; 2018</span><br />
          <span style={{ color: '#FFCD34', opacity: '0.7'}}><i>ntinvo@gmail.com</i></span>
        </div>
      </div>
    )
  }
}

export default Socials



