import React, { Component } from 'react'
import './Intro.css'
import $ from 'jquery'
import Navbar from './Navbar'
import Typed from 'typed.js'
import wavinghandMD from '../../assets/wavinghand-md.png'
import wavinghandXS from '../../assets/wavinghand-xs.png'
import workXS from '../../assets/work-xs.png'
import pizzaXS from '../../assets/pizza-xs.png'
import bikingXS from '../../assets/biking-xs.png'
import gameXS from '../../assets/game-xs.png'
import runnerXS from '../../assets/runner-xs.png'
import debuggerXS from '../../assets/debugger-xs.png'

class Intro extends Component {
  constructor(props) {
    super(props)
    this.state = {
      wavinghand : wavinghandMD,
      work: workXS,
      pizza: pizzaXS,
      biking: bikingXS,
      game: gameXS,
      runner: runnerXS,
      debugger: debuggerXS
    }
  }

  componentDidMount() {
    var string_1 = 'developer ' + '<img src=' + this.state.work + ' style=\'max-height: 28px; max-width: 28px;\'>'
    var string_2 = 'debugger ' + '<img src=' + this.state.debugger + ' style=\'max-height: 28px; max-width: 28px;\'>'
    var string_3 = 'gamer ' + '<img src=' + this.state.game + ' style=\'max-height: 28px; max-width: 28px;\'>'
    var string_4 = 'bicycler ' + '<img src=' + this.state.biking + ' style=\'max-height: 28px; max-width: 28px;\'>'
    var string_5 = 'runner ' + '<img src=' + this.state.runner + ' style=\'max-height: 28px; max-width: 28px;\'>'
    var string_6 = 'pizza eater ' + '<img src=' + this.state.pizza + ' style=\'max-height: 28px; max-width: 28px;\'>'

    var typed = new Typed('.typed_text', {
      strings: [string_1, string_2, string_3, string_4, string_5, string_6],
      typeSpeed: 45,
      shuffle: false,
      fadeOut: true,
      loop: true,
      loopCount: Infinity,
      showCursor: true,
    });

    if($(document).width() < 990) {
      this.setState({wavinghand: wavinghandXS});
    }
  }

  render() {
    return (
      <div className='intro'>
        <Navbar />
        <div className='inner'>
          <div className='content'>
            <h1 className='main'><strong>Hello <img src={this.state.wavinghand} className='wavinghand' alt='wavinghand' />, my name is Tin Vo. I am a </strong><br /><span className='typed_text'></span></h1>
          </div>
        </div>
      </div>
    )
  }
}

export default Intro



