import React, { Component } from 'react'
import './Contact.css'

class Contact extends Component {
  render() {
    return (
      <div id='contact' className='section-area text-center' >
        <h2>CONTACT</h2><br/>
        <div className='lead lead-contact'><i>Question? Send me a message.</i></div>
        <div className='container text-center contactformdiv'>
          <form className='contactform justify-content-center'>
            <div className='form-group'>
              <input type='name' className='form-control contactforminput' id='exampleFormControlInput1' placeholder='Enter your name' required/>
            </div>
            <div className='form-group'>
              <input type='email' className='form-control contactforminput' id='exampleFormControlInput2' placeholder='Enter your email' required/>
            </div>
            <div className='form-group'>
              <textarea className='form-control contactforminput textarea' id='exampleFormControlTextarea1' rows='3'  placeholder='Your message' required></textarea>
            </div>
            <button type='submit' className='btn btn-secondary send-button'>SEND</button>
          </form>
        </div>
        <br />
      </div>
    )
  }
}

export default Contact



