import React, { Component } from 'react'
import { connect } from 'react-redux'
import './Navbar.css'
import $ from 'jquery'


class Navbar extends Component {

  componentDidMount() {
    if($(document).width() > 990) { this.changeNavbarBackground() }
    this.navbarCollapse()
    this.scrollToSection()
    this.scrollToTop()
    this.resize()
  }

  changeNavbarBackground() {
    $(document).scroll(function() {
      if(document.documentElement.scrollTop > 20) {
        var speed = (document.documentElement.scrollTop + 20) / 250
        $('.navbar').css('background-color', 'rgba(47,52,60,' + speed + ')')
        // $('.navbar').css('border-bottom', '0px solid #00FFFF')
      } else {
        $('.navbar').css('background-color', 'rgba(47,52,60,0)')
        // $('.navbar').css('border-bottom', '2px solid #00FFFF')
      }
    })
  }

  navbarCollapse() {
    $('.navbar-collapse a').click(function(){
        $('.navbar-collapse').collapse('hide')
    })
  }

  scrollToSection() {
    $('.slide-section').click(function(e) {
      var navbarHeight = $('.navbar-toggler').outerHeight();
      if($(this).text() !== 'RESUME') {
        var sectionToScrollTo = $(this).attr('href');
        $('html, body').animate({
          scrollTop: $(sectionToScrollTo).offset().top - navbarHeight - 13
        });
        e.preventDefault()
      }
    })
  }

  scrollToTop() {
    $('.navbar-brand').click(function(e) {
      $('.navbar-collapse').collapse('hide')
      $('html, body').animate({
        scrollTop: 0
      }, 1000, function() {})
      e.preventDefault()
    })
  }

  resize() {
    $(window).resize(function() {
      if($(document).width() < 990) {
        $('.navbar').css('background-color', 'rgba(47,52,60,1.0)');
      } else {
        $('.navbar').css('background-color', 'rgba(47,52,60,0.0)');
      }
    });
  }

  render() {
    const styles = {
      backgroundColor: `${this.props.nav.backgroundColor}`,
      borderBottom: `${this.props.nav.borderBottom}`,
      color: `${this.props.nav.textColor}`
    }
    const sectionStyles = {
      color: `${this.props.nav.textColor}`
    }

    return (
      <nav className='navbar navbar-expand-lg navbar-light fixed-top' style={styles}>
        <a className='navbar-brand' href='#' style={sectionStyles}>TIN N VO</a>
        <button className='navbar-toggler' type='button' data-toggle='collapse' data-target='#navbarSupportedContent' aria-controls='navbarSupportedContent' aria-expanded='false' aria-label='Toggle navigation'>
          <span className='navbar-toggler-icon'></span>
        </button>

        <div className='collapse navbar-collapse navbar-right' id='navbarSupportedContent'>
          <ul className='navbar-nav ml-auto'>
            <li className='nav-item active'>
              <a className='nav-link slide-section' href='#aboutme' style={sectionStyles} >ABOUT ME</a>
            </li>
            <li className='nav-item'>
              <a className='nav-link slide-section' href='#experience' style={sectionStyles}>EXPERIENCE</a>
            </li>
            <li className='nav-item'>
              <a className='nav-link slide-section' href='#projects' style={sectionStyles}>PROJECTS</a>
            </li>
            <li className='nav-item'>
              <a className='nav-link slide-section' href='#skills' style={sectionStyles}>SKILLS</a>
            </li>
            <li className='nav-item'>
              <a className='nav-link slide-section' href='#contact' style={sectionStyles}>CONTACT</a>
            </li>
            <li className='nav-item'>
              <a className='nav-link slide-section' href={process.env.PUBLIC_URL + '/docs/TinVo-Resume.pdf'} target='_blank' rel='noopener noreferrer' style={sectionStyles}>RESUME</a>
            </li>
          </ul>
        </div>
      </nav>
    )
  }
}

const mapStateToProps = state => {
  return {
    nav: state.NavbarReducer
  }
}

export default connect(mapStateToProps)(Navbar)



